@import "../../base/variables.scss";

.range-slider__top {
	display: flex;
	justify-content: space-between;
	margin-bottom: 17px;
}

.range-slider__output {
	font-size: 12px;
	line-height: 16px;
	text-align: right;
	text-transform: uppercase;
	color: $color-ds50;

	background: transparent;
	border: none;
}

.range-slider__description {
	margin-top: 14px;
	margin-bottom: 0;

	font-size: 12px;
	line-height: 14px;
	color: $color-ds50;


}

.range-slider__label {
	margin-bottom: 0;
	line-height: 24px;
	min-width: 123px;
}

.ui-slider.ui-slider-horizontal {
	position: relative;
	z-index: 0;

	box-sizing: border-box;
	height: 6px;

	background: #FFFFFF;
	border: 1px solid $color-ds25;
	border-radius: 3px;

	& .ui-slider-handle {
		position: absolute;
		top: -6px;
		z-index: 2;


		width: 16px;
		height: 16px;
		margin-left: -.6em;

		background: $color-green-gradient;
		border: 2px solid #FFFFFF;
		border-radius: 50%;

		-ms-touch-action: none;
		touch-action: none;
		cursor: pointer;
	}

	& .ui-slider-range {
		position: absolute;
		z-index: 1;

		width: 88px;
		height: 6px;

		background: $color-green-gradient;
		border-radius: 3px;
	}

	& .ui-slider-range {
		top: -1px;
		height: 6px;
	}
}